<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <div style="margin: 16px;">
        <div>current page: testA</div>
      </div>

      <div style="margin: 16px;">
        <div>原生跳转</div>
      </div>
      <div style="margin: 16px;">
        <van-button type="primary" round block @click="next">下一页</van-button>
      </div>

      <div style="margin: 16px;">
        <div>h5内部跳转</div>
      </div>
      <div style="margin: 16px;">
        <van-button type="primary" round block @click="h5next">下一页</van-button>
      </div>

      <!-- <van-pull-refresh v-model="isLoading" success-text="刷新成功" @refresh="onRefresh">
      <p>刷新次数: {{ count }}</p>
      </van-pull-refresh>-->

      <p>刷新次数: {{ count }}</p>
    </van-pull-refresh>
  </div>
</template>

<script>
import xengine from "engine-zkty";
import { Toast } from "vant";
export default {
  name: "testA",
  components: {},
  data() {
    return {
      count: 0,
      isLoading: false,
    };
  },
  methods: {
    next() {
      xengine.nav.navigatorPush({
        url: "/testB",
        params: "000",
      });
    },

    h5next() {
      this.$router.push({
        path: "/testB",
        query: { qid: "110" },
      });
    },

    onRefresh() {
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
  },
};
</script>

